import React, { useState } from 'react';
import { getAuth, sendSignInLinkToEmail, signInWithEmailLink, isSignInWithEmailLink } from "firebase/auth";

function EmailBox({ onGoBack }) {
const [email, setEmail] = useState('');
const [message, setMessage] = useState('');
const [messageColor, setMessageColor] = useState('');

const actionCodeSettings = {
url: 'https://www.tioassist.io/',
handleCodeInApp: true,
};

const auth = getAuth();

const handleSignup = () => {
sendSignInLinkToEmail(auth, email, actionCodeSettings)
.then(() => {
window.localStorage.setItem('emailForSignIn', email);
setMessage('Email sent successfully. Please check your inbox');
setMessageColor('green');
})
.catch((error) => {
const errorMessage = "An error occurred while sending the email.";
setMessage(errorMessage);
setMessageColor('red');
});
};

const handleVerification = async () => { // Add async here
const emailForSignIn = window.localStorage.getItem('emailForSignIn');
const signInEmailLink = window.location.href;

if (isSignInWithEmailLink(auth, signInEmailLink)) { // Use isSignInWithEmailLink here
let email = emailForSignIn;
if (!email) {
email = window.prompt('Please provide your email for confirmation');
}
try {
const result = await signInWithEmailLink(auth, email, signInEmailLink); // Await here
window.localStorage.removeItem('emailForSignIn');
} catch (error) {
console.error("Error during email link sign-in:", error);
}
}
};

const handleKeyPress = (event) => {
if (event.key === 'Enter') {
event.preventDefault();
handleSignup();
const submitButton = document.getElementById('btnSignup');
if (submitButton) {
submitButton.focus();
}
}
};



return (
<div>
<div className="login-box" id="loginBox">
<div className="log-in-text">
<div className="opening-second">
<div className="go-back">
<button className="go-back-button" onClick={onGoBack}>
<i className="fas fa-backspace"></i>
</button>
<div className="center-text">
<h4 className="log-in-text-heading">Sign-Up with only your Email</h4>
</div>
</div>
</div>
</div>
<h6 className="log-in-text-closing">
<p className="fw-light">Let's sign you up so you can get the answers you need!</p>
</h6>
<form id="signupForm">
<div className="signup-form">
<div className="col-lg-12 mb-3">
<input
type="email"
placeholder="email address"
id="input-Email"
className="shadow form-control form-control-lg"
autoComplete="email"
value={email}
onChange={(e) => setEmail(e.target.value)}
onKeyDown={handleKeyPress} // Call handleKeyPress on key press
/>
</div>

<div className="col-lg-12 col-md mb-3" style={{ paddingBottom: '15px' }}>
<button
type="button"
className="btn btn-primary rounded-pill pt-2 pb-2"
style={{ width: 'calc(100% - 1.5rem)' }}
id="btnSignup"
onClick={handleSignup}
>
submit <i className="fas fa-paper-plane"></i>
</button>
</div>
{/* Display the message */}
{message && <p className="fw-light" style={{ color: messageColor }}>{message}</p>}
</div>
</form>
</div>
</div>
);
}

export default EmailBox;

